
.grid {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.grid .grid-item {
  width: 75px;
  margin: 10px;
  position: relative;
}
.grid .grid-item:active {
  background-color: aquamarine;
}
.grid .touch {
  position: absolute;
  height: 100%;
  width: 100%;
}
.grid img {
  height: 45px;
  margin: 0 15px;
}
.grid p {
  width: 75px;
  text-align: center;
  font-size: 0.9rem;
}
.grid .choose {
  position: absolute;
  height: 20px;
  width: 20px;
  top: -8px;
  right: 8px;
  line-height: 20px;
  text-align: center;
  font-size: 20px;
  color: teal;
}
.delate {
  position: absolute;
  right: 0;
  top: 0;
  background-color: #f8f8f8;
  color: blue;
  width: 60px;
  text-align: center;
  height: 45px;
  line-height: 45px;
}



